:root {
  --color-dark-text: #494450;
  --color-text: #615c69;
  --color-light-text: #d2bea6;
  --font-bebas: 'Bebas Neue', Arial, Helvetica, sans-serif;
  --bg-body: #f1e2cf;
  --bg-light: #e8d7c3;
  --bg-dark: #494450;
  --transition: 0.2s ease-in-out;
}

@font-face {
  font-family: 'Bebas Neue';
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/BebasNeueBold.woff2) format('woff2'), url(../fonts/BebasNeueBold.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Black.woff2) format('woff2'), url(../fonts/Montserrat-Black.woff) format('woff');
}

@font-face {
  font-family: 'Montserrat';
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url(../fonts/Montserrat-Medium.woff2) format('woff2'), url(../fonts/Montserrat-Medium.woff) format('woff');
}

html {
  min-width: 320px;
  height: 100%;
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  padding-top: 82px;
  font-weight: 500;
  font-size: 13px;
  font-family: 'Montserrat', Arial, Helvetica, sans-serif;
  color: var(--color-text);
  background-color: var(--bg-body);
}

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
  border-bottom: none;
}

b,
strong {
  font-weight: bolder;
}

small {
  font-size: 80%;
}

img {
  max-width: 100%;
}

button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font-size: 100%;
  line-height: 1.15;
  font-family: inherit;
}

button,
select {
  text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  display: table;
  box-sizing: border-box;
  max-width: 100%;
  padding: 0;
  color: inherit;
  white-space: normal;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

details {
  display: block;
}

summary {
  display: list-item;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

.col {
  box-sizing: border-box;
  min-width: 320px;
  max-width: 1620px;
  margin-right: auto;
  margin-left: auto;
  padding-right: 30px;
  padding-left: 30px;
}

.page-content {
  width: 100%;
  max-width: 1100px;
}

.emoji {
  display: inline-block;
  vertical-align: text-bottom;
}

.btn {
  display: inline-block;
  padding: 15px 50px 12px;
  font-weight: 700;
  font-size: 22px;
  line-height: 1;
  font-family: var(--font-bebas);
  text-align: center;
  color: var(--color-light-text);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2.5px;
  background-color: var(--bg-body);
  border: 0;
  border: 1px solid #d1bda5;
  border-radius: 26px;
  transition: box-shadow var(--transition);
  appearance: none;
}

.btn:hover {
  box-shadow: 0px 4px 20px 0px var(--color-light-text);
}

.btn:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
  box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.btn:active {
  box-shadow: 0px 2px 4px 0px transparent;
}

.btn--success {
  color: var(--color-light-text);
  background-color: var(--bg-dark);
  border: 1px solid var(--bg-dark);
  box-shadow: 0px 4px 16px 0px #3a2b4f;
  transition: box-shadow var(--transition);
}

.btn--success:hover {
  box-shadow: 0px 2px 4px 0px #3a2b4f;
}

.btn--success:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: -1px;
}

.btn--success:active {
  box-shadow: 0px 2px 4px 0px transparent;
}

.btn--text-small {
  letter-spacing: 1px;
}

.btn:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

.btn {
  display: inline-block;
  vertical-align: middle;
}

.btn:disabled {
  opacity: 0.6;
  box-shadow: none;
}

.page-header {
  position: relative;
  z-index: 2;
  color: var(--color-light-text);
}

.page-footer__col {
  padding-right: 30px;
  padding-left: 30px;
}

.page-header__top {
  position: relative;
  padding: 15px;
  text-align: center;
  background-color: var(--bg-dark);
}

.page-header__nav-toggle {
  width: 24px;
  height: 22px;
  padding: 0;
  background-color: transparent;
  background-image: url(../img/icon/page-header-toggle.svg);
  border: 0;
  box-shadow: none;
  transition: opacity var(--transition);
}

.page-header__nav-toggle:hover {
  opacity: 0.2;
}

.page-header__nav-toggle:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: 2px;
}

.page-header__logo:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #d1bda5;
  outline-offset: 2px;
}

.page-header__toggle {
  background-color: var(--bg-dark);
}

.page-header__user-wrap {
  display: flex;
  justify-content: center;
  width: 100%;
  background-color: #55505d;
}

.page-header__user-wrap > div {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  min-width: 320px;
  max-width: 320px;
  padding: 24px 30px 24px 24px;
}

.page-header__user-name {
  display: flex;
  align-items: center;
  margin: 0;
  font-weight: 700;
  font-size: 20px;
  font-family: var(--font-bebas);
  color: var(--color-light-text);
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  transition: opacity var(--transition);
}

.page-header__user-name:hover {
  opacity: 0.7;
}

.page-header__user-name:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #7b7682;
  outline-offset: 2px;
}

.page-header__user-ava {
  display: block;
  width: 60px;
  height: 60px;
  margin-right: 10px;
  background-color: #686370;
  background-image: url(../img/ava_default.svg);
  background-repeat: no-repeat;
  background-position: center;
  border-radius: 50%;
}

.page-header__user-exit {
  font-size: 20px;
  font-family: var(--font-bebas);
  color: #7b7682;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  border-bottom: 1px dashed #7b7682;
  transition: opacity var(--transition);
}

.page-header__user-exit:hover {
  opacity: 0.7;
}

.page-header__user-exit:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #7b7682;
  outline-offset: 2px;
}

.page-header__nav {
  box-sizing: border-box;
  min-width: 320px;
  padding: 23px 40px;
  border-bottom: 1px solid #504b58;
}

.page-header__nav-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.page-header__nav-item {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  align-items: baseline;
}

.page-header__nav-item svg {
  margin-right: 32px;
  transition: transform var(--transition), fill var(--transition);
  fill: var(--color-text);
}

.page-header__nav-item a {
  width: 100%;
  font-weight: 700;
  font-size: 26px;
  line-height: 28px;
  padding: 11px 0;
  font-family: var(--font-bebas);
  color: var(--color-light-text);
  text-decoration: none;
  letter-spacing: 2px;
}

.page-header__nav-item a:hover svg {
  transform: translateX(4px);
  fill: var(--color-light-text);
}

.page-header__nav-item a:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #7b7682;
  outline-offset: 4px;
}

.page-header__bottom {
  padding: 25px;
}

.page-header__bottom p {
  margin: 0 0 24px;
  line-height: 22px;
  color: #7b7682;
}

.page-header__bottom-btn {
  color: #7b7682;
  background-color: inherit;
  border-color: #7b7682;
}

.page-header__bottom-btn:hover {
  box-shadow: 0px 4px 20px 0px #7b7682;
}

.page-header__bottom-btn:focus-visible {
  outline-width: 2px;
  outline-style: double;
  outline-color: #7b7682;
  outline-offset: -1px;
  box-shadow: 0 0 0 0 transparent;
}

.page-header__bottom-btn:active {
  box-shadow: 0 0 0 0 transparent;
}

.page-footer {
  --color-footer-text: #85818c;
  font-size: 12px;
  line-height: 16px;
  color: var(--color-footer-text);
  background-color: var(--bg-dark);
  flex-grow: 0!important;
}

.page-footer a {
  color: inherit;
}

@media (min-width: 576px) {
  .hidde-min-sm {
    display: none;
  }
}

@media (min-width: 768px) {
  body {
    padding-top: 0;
  }

  .hidde-min-md {
    display: none !important;
  }

  .page-grid {
    display: grid;
    flex-grow: 1;
    align-content: stretch;
    grid-template-columns: 1fr 110px 40px minmax(320px, 1100px) 1fr;
  }

  .page-header {
    display: grid;
    align-self: stretch;
    grid-column-end: 3;
    grid-column-start: 1;
    grid-template-columns: 1fr 110px;
    height: 100%;
  }

  .page-header__wrap {
    grid-column-end: 3;
    grid-column-start: 2;
  }

  .page-header__content {
    position: sticky;
    top: 0;
    max-height: 100vh;
    overflow: auto;
  }

  .page-main {
    grid-column-end: 5;
    grid-column-start: 4;
  }

  .page-footer__col {
    grid-column-end: 5;
    grid-column-start: 4;
    width: 100%;
    padding-left: 0;
  }

  .col {
    padding-right: 40px;
    padding-left: 40px;
  }

  .page-footer__col {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }

  .page-footer__page-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .page-footer__social-list {
    display: flex;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  .page-footer__social-item:not(:last-child) {
    margin-right: 15px;
  }

  .page-footer__social-link {
    display: flex;
    align-items: center;
    width: 20px;
    height: 20px;
  }

  .page-footer__social-link:focus-visible {
    outline-width: 2px;
    outline-style: double;
    outline-color: var(--color-footer-text);
    outline-offset: 2px;
  }

  .page-footer__social-link svg {
    fill: var(--color-footer-text);
  }

  .page-footer__social-link--instagram:hover svg {
    fill: url(#a);
  }

  .page-footer__social-link--vk svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--vk:hover svg {
    fill: #2787f5;
  }

  .page-footer__social-link--vk:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--youtube svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--youtube:hover svg {
    fill: #ff0000;
  }

  .page-footer__social-link--youtube:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--telegram svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--telegram:hover svg {
    fill: url(#a1);
  }

  .page-footer__social-link--telegram:hover svg .center {
    fill: #fff;
  }

  .page-footer__social-link--facebook svg .center {
    fill: var(--bg-dark);
  }

  .page-footer__social-link--facebook:hover svg {
    fill: #1877f2;
  }

  .page-footer__social-link--facebook:hover svg .center {
    fill: #fff;
  }
}

@media (min-width: 990px) {
  .hidde-min-lg {
    display: none;
  }
}

@media (min-width: 1200px) {
  .hidde-min-xl {
    display: none;
  }

  .page-grid {
    grid-template-columns: 1fr 400px 80px minmax(320px, 1100px) 1fr;
  }

  .page-header {
    grid-template-columns: 1fr 400px;
  }

  .page-header__top {
    padding: 24px 40px;
  }

  .page-header__logo img {
    width: 222px;
    height: 73px;
  }
}

@media (min-width: 1620px) {
  .hidde-min-xxl {
    display: none;
  }
}

@media (max-width: 1619px) {
  .hidde-max-xxl {
    display: none;
  }
}

@media (max-width: 1199px) {
  .hidde-max-xl {
    display: none;
  }

  .page-header__nav-toggle {
    position: absolute;
    top: 50%;
    right: 30px;
    transform: translateY(-50%);
  }
}

@media (max-width: 989px) {
  .hidde-max-lg {
    display: none;
  }
}

@media (max-width: 767px) {
  .hidde-max-md {
    display: none;
  }

  .page-header__top {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
  }

  .page-header__toggle {
    position: fixed;
    top: 82px;
    right: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    max-height: calc(100vh - 82px);
    overflow-y: auto;
    transform: translateX(-100vw);
    transition: transform 0.5s ease-in-out;
  }

  .page-header__open {
    height: 100%;
    max-height: 100%;
  }

  .page-header__open .page-header__toggle {
    transform: translateX(0vw);
  }

  .page-footer__col {
    padding: 0;
  }

  .page-footer__nav-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
  }

  .page-footer__nav-item {
    position: relative;
    flex-grow: 1;
    box-sizing: border-box;
  }

  .page-footer__nav-item svg {
    display: block;
    margin: 13px auto 10px;
    fill: var(--color-text);
  }

  .page-footer__nav-item:not(:last-child) {
    border-right: 1px solid #504b58;
  }

  .page-footer__nav-link {
    display: block;
    padding: 10px 5px 8px;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;
    font-family: var(--font-bebas);
    text-align: center;
    color: var(--color-light-text);
    text-transform: uppercase;
    text-decoration: none;
    letter-spacing: 2px;
    transition: color var(--transition);
  }

  .page-footer__nav-link.active {
    color: var(--color-text);
  }

  .page-footer__nav-link:hover {
    color: var(--color-text);
  }

  .page-footer__nav-link:focus-visible {
    outline-width: 1px;
    outline-style: double;
    outline-color: #d1bda5;
    outline-offset: -2px;
  }

  .page-footer__nav-link.active::after {
    content: url(../img/icon/page-footer__nav-link-active.svg);
    position: absolute;
    top: -13px;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media (max-width: 575px) {
  .hidde-max-sm {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 1199px) {
  .page-header {
    position: fixed;
    display: block;
    width: 110px;
    transition: width var(--transition);
  }

  .page-header__open {
    width: 400px;
  }

  .page-header__close .page-header__content {
    overflow-x: hidden;
  }

  .page-header__close .page-header__top {
    padding: 30px;
  }

  .page-header__close .page-header__logo {
    display: none;
  }

  .page-header__close .page-header__nav-toggle {
    right: 50%;
    transform: translate(50%, -50%);
  }

  .page-header__close .page-header__user-wrap > div {
    min-width: initial;
  }

  .page-header__close .page-header__user-name {
    font-size: 0;
    letter-spacing: normal;
  }

  .page-header__close .page-header__user-ava {
    margin-right: 0;
  }

  .page-header__close .page-header__user-exit {
    display: none;
  }

  .page-header__close .page-header__nav {
    min-width: initial;
  }

  .page-header__close .page-header__nav-item a {
    font-size: 0;
    line-height: 0;
  }

  .page-header__close .page-header__bottom-notes {
    display: none;
  }

  .page-header__close .page-header__bottom-btn {
    position: relative;
    padding: 0;
    font-size: 0;
    width: 60px;
    height: 60px;
    border-radius: 50%;
  }

  .page-header__close .page-header__bottom-btn:hover {
    background-color: var(--color-text);
    box-shadow: none;
    border-color: var(--color-text);
  }

  .page-header__close .page-header__bottom-btn:hover::before {
    color: var(--bg-dark);
  }

  .page-header__close .page-header__bottom-btn::before {
    content: '?';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 40px;
    line-height: 60px;
  }
}